class JSLangBox extends React.Component {
    render() {
        return (
            <LangBox
                langname={"JavaScript"}
                icon={js_icon}
                comment={"Github\n\nReact"}
            />
        )
    }
}

export class SkillPage extends React.Component {
    render() {
        return (
            <div class="skillpage">
                <div class="skillpage-comment">
                    <br />
                    Github<br />
                </div>
                <CppLangBox />
                <CLangBox />
                <PyLangBox />
                <HtmlCssLangBox />
                <JSLangBox />
            </div>
        );
    }
}